.repository-list-container {
    display:flex;
    flex-direction: column;
    padding: 5vw;
    flex-direction: row;
    
    @media screen and (min-width: 768px) {
        flex-direction: row;
    }
}

.repo-list {
    flex: 1;
    align-items: center;
    justify-self: end;
    font-size:small;
    
  
    @media screen and (min-width: 768px) {
      width: 50%;
    }
}

.repository-item {
    position: absolute;
    padding-top: 1.2vh;
    padding-bottom:1.2vh ;
    cursor: pointer;
    position: relative;

    color: white;
    
    font-family: 'P2P';
}
  
.repository-item.selected {
    text-shadow: 0 0 15px #ffff00, 0 0 25px #ffff00;
    animation: glow 2s linear infinite;
}

.selected-repo-container{
    position: relative;
    font-family: 'Prompt';
    
    flex: 1;
    flex-basis: 9%;  

    place-items:inherit;
    cursor: default;
    
    @media screen and (min-width: 768px) {
        width: 50%;
    }
}

.selected-repo p{
    position: inherit;
    justify-self: right;
    text-align: left;
    color: white;
    
    font-size: medium;
}

.btn-git-url{
    position: inherit;
    border-radius: 10px;
    background:#1a031d8f;

    justify-items: center;
    align-items: center;

    cursor:pointer;

    border-color: transparent;
}
.initial-message{
    color: white;
    font-size: larger;
}

@keyframes glow {
    100% {
        filter: hue-rotate(360deg);
    }   
}
