.App {
  text-align: center;
  padding: 0%;
  margin:0%;
  background: linear-gradient( #8e53aca9 , #6a627a);
}

.App-header {
  min-height: 50vh;
  min-width: 50vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-logo{
  pointer-events: none;
  border-radius: 100%;
  background: transparent;
  border-color: #000000;
}

.App-button {
  border-radius: 100%;
  border: none;
  border-color: transparent;
  background-color: #0a053596;
}
p{
  font-family: 'Pangolin';
  cursor: default;
}
div{
  cursor: default;
}